$full:100%;
@mixin  flex($justify:center,$align:center,$wrap:wrap,$direction:row) {
    display: flex;
    justify-content: $justify;
    align-items: $align;
    flex-wrap: $wrap;
    flex-direction: $direction;
}
html,body{
    padding: 0 !important;
    margin: 0 !important;
}
i{
    padding: 10px;
    font-size:20px;
    color:red;
   &:hover{
       color:blue;
   }
}
.layout{
    width:$full;
    height:$full;
    .header{
        @include flex(flex-start,center,row);
        padding-left:20px;
        .logo{
            width:13%;
            height:90%;
            background: url('./assets/img/logo.png.png') no-repeat;
            background-size:100% 100%;
        }
        .title{
            color:white;
            display: block;
            width:30%;
            height:$full;
            text-align: center;
            font:{
                size:24px;
                weight:bold;
            }
        }
    }
}
.LoginPage{
    width:$full;
    height:$full;
    background: url('./assets/img/login_bg.png.jpg') no-repeat;
    background-size:100% 100%;
    @include flex(center,center,row);
}
.LoginForm{
    width:308px;
    height:25vh;
    @include flex(center,center,nowrap,column);
    background: rgba(255, 255, 255, .3);
    border-radius: 5px;
    overflow: hidden;
    position: relative;
    text-shadow: #eee 1px 0 0, #eee 0 1px 0, #eee -1px 0 0, #eee 0 -1px 0;
    .loginIn{
        color:#d71a5d;
        text-shadow: #eee 1px 0 0, #eee 0 1px 0, #eee -1px 0 0, #eee 0 -1px 0;
        padding: 10px;
        font-size:20px;
        font-weight:bold;
    }
    &::before{
        content: '';
        position: absolute;
        top: 0; bottom: 0;
        left: 0; right: 0;
        filter: blur(50px);
        -webkit-filter: blur(20px);
        -moz-filter: blur(20px);
        -ms-filter: blur(20px);
        -o-filter: blur(20px);
        z-index: -1; 
        margin: -30px;
    }
}